import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			orderTotalPrice:0,//总价格
			goodsId:0,//商品id
			goodsSkuId:0,//规则
			token:'',
			address:{},//地址
			orderId:null,//订单ID
			freight:null,//运费
			quantityNumber:'',//商品数量
			commodityPrice:'',//商品价格
			addressId:'',//收货地址id 
			orderNo:'',//订单编号
		};
	},
	async onLoad(options){
		let self = this;
		console.log(options)
		self.orderId = options.id;
		self.loadData();
		//获取地址
		// self.getAdressDetail();
		//获取token
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res.data);
				self.token = res.data.token;
			}
		});
		
		console.log(self.hasLogin);
	},
	
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData(){
			let self = this;
			self.$axios(self.$baseUrl.orderDetails,{
				id:self.orderId
			})
			.then(res =>{
				let result = res.data.data.order;
				console.log(11111111,result)
				if(res.data.code == 1){
					self.cartList =  result.goods; //订单
					self.address =  result.address;//地址
					self.freight = result.express_price;//运费
					self.orderTotalPrice = result.pay_price;//总价
					self.quantityNumber = result.total_num;//购买商品数量
					self.commodityPrice = result.total_price;//商品价格
					self.orderNo = result.order_no;//订单编号
					
				}
				if(res.data.code == 0){
					uni.navigateBack({
						url: `pages/order/order`
					})
				}
				
			},(error) =>{
				console.log(error)
			})
			let list = await this.$api.json('cartList'); 
			let cartList = list.map(item=>{
				item.checked = true;
				return item;
			});
			
			// this.calcTotal();  //计算总价
		},
		onShow() { //返回显示页面状态函数
				//，来进行局部刷新
				 let pages = getCurrentPages();
		         let currPage = pages[pages.length-1];
		         console.log(111,currPage.data.addressId)
		         if (currPage.data.addressId !=""){
			        //更换地址
			        this.getAdressDetail(currPage.data.addressId );
		         }
		
			},
		//更换地址
		getAdressDetail(id){
			let self = this;
			self.$axios(self.$baseUrl.getAdressDetail,{
				id:id
			})
			.then(res =>{
				let result = res.data.data;
				console.log(result)
				if(res.data.code == 1){
					self.address =  result.detail;//地址
					self.addressId = result.detail.address_id;
					
				}else if(res.data.code == 0){
					uni.showToast({title:'请选择收货地址',icon:'success'});
				}
				
				
			},(error) =>{
				console.log(error)
			})
		
			
		},
		
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		
		//数量
		numberChange(data){
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(todo){
			let self = this;
			console.log(11111111111,todo)
			let goodsId = todo.goods_id;
			let goodsSkuId = todo.goods_sku_id;
			uni.showModal({
				title: '删除提示',
				content: '你将删除这个商品',
				success: (res)=>{
					if (res.confirm) {
						self.$axios(self.$baseUrl.delCart,{
							goods_id:goodsId,
							goods_sku_id:goodsSkuId
						})
						.then(res =>{
							
							if(res.data.code == 1){
								uni.showToast({title:'删除成功',icon:'success'});
							}
							
						},(error) =>{
							console.log(error)
						})
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart(){
			let self = this;
			uni.showModal({
				title: '清空提示',
				content: '你要清空购物车里面的商品吗？',
				success: (res)=>{
					if (res.confirm) {
						self.$axios(self.$baseUrl.clearAllCart,{
							token:self.token
						})
						.then(res =>{
							
							if(res.data.code == 1){
								uni.showToast({title:'删除成功',icon:'success'});
								//进行局部刷新
								this.loadData();
							}
							
						},(error) =>{
							console.log(error)
						})
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		//结算
		createOrder(){
			let self = this;
			self.$axios(self.$baseUrl.orderPay,{
				id:self.orderId
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					 // 发起微信支付
					wx.requestPayment({
					  'timeStamp': result.timestamp,
					  'nonceStr': result.nonceStr,
					  'package': result.package,
					  'signType': result.signType,
					  'paySign': result.paySign,
					  success: function (res) {
						console.log('支付成功');
						
						// 跳转到订单展示界面
						/* wx.redirectTo({
						  url: '../order/index?showType=2'
						}) */
					  },
					  fail: function (res) {
						console.log(res);
						App.showError('订单未支付', function () {
						  // 跳转到未付款订单展示界面
						  /* wx.redirectTo({
							url: '../order/index?showType=1'
						  }) */
						});
					  },
					});
					
				}
				
				
			},(error) =>{
				console.log(error)
			})
		},
		//更改地址
		changeAddress(){
			console.log(1111+'更改地址')
			uni.navigateTo({
				url: `/pages/user/address/address`
			})
		},
	     
		//商品详情页面
		commodityDetails(id){
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
			
	}
}
